"use client"

import { useSession, signOut } from "next-auth/react"
import { useRouter, usePathname } from "next/navigation"
import { useEffect, useState } from "react"
import Link from "next/link"
import { 
  Shield, 
  LayoutDashboard, 
  Users, 
  Truck, 
  Package, 
  Calendar,
  LogOut,
  Menu,
  ChevronRight
} from "lucide-react"
import { cn } from "@/lib/utils"
import { Button, Sheet, SheetContent, SheetTrigger, Skeleton } from "@/components/ui"

const navItems = [
  { href: "/admin", label: "Dashboard", icon: LayoutDashboard },
  { href: "/admin/customers", label: "Customers", icon: Users },
  { href: "/admin/delivery-persons", label: "Delivery Team", icon: Truck },
  { href: "/admin/inventory", label: "Inventory", icon: Package },
  { href: "/admin/holidays", label: "Holidays", icon: Calendar },
]

function SidebarContent({ pathname, onNavigate }: { pathname: string; onNavigate?: () => void }) {
  const { data: session } = useSession()

  return (
    <div className="flex flex-col h-full">
      {/* Logo */}
      <div className="flex items-center gap-3 p-4 border-b border-[hsl(var(--border))]">
        <div className="w-10 h-10 bg-slate-800 rounded-xl flex items-center justify-center">
          <Shield className="w-5 h-5 text-white" />
        </div>
        <div>
          <p className="font-semibold text-[hsl(var(--foreground))]">Maa Ilay</p>
          <p className="text-xs text-[hsl(var(--muted-foreground))]">Admin Portal</p>
        </div>
      </div>

      {/* Navigation */}
      <nav className="flex-1 p-3 space-y-1 overflow-y-auto">
        {navItems.map((item) => {
          const isActive = pathname === item.href || 
            (item.href !== "/admin" && pathname.startsWith(item.href))
          const Icon = item.icon

          return (
            <Link
              key={item.href}
              href={item.href}
              onClick={onNavigate}
              className={cn(
                "flex items-center gap-3 px-3 py-2.5 rounded-lg text-sm font-medium transition-colors",
                isActive 
                  ? "bg-slate-900 text-white" 
                  : "text-[hsl(var(--muted-foreground))] hover:bg-[hsl(var(--muted))] hover:text-[hsl(var(--foreground))]"
              )}
            >
              <Icon className="w-5 h-5" />
              <span>{item.label}</span>
              {isActive && <ChevronRight className="w-4 h-4 ml-auto" />}
            </Link>
          )
        })}
      </nav>

      {/* User Section */}
      <div className="p-4 border-t border-[hsl(var(--border))]">
        <div className="flex items-center gap-3 mb-3">
          <div className="w-10 h-10 bg-[hsl(var(--muted))] rounded-full flex items-center justify-center flex-shrink-0">
            <span className="text-sm font-semibold text-[hsl(var(--foreground))]">
              {session?.user?.name?.charAt(0).toUpperCase() || "A"}
            </span>
          </div>
          <div className="min-w-0 flex-1">
            <p className="text-sm font-medium text-[hsl(var(--foreground))] truncate">
              {session?.user?.name || "Admin"}
            </p>
            <p className="text-xs text-[hsl(var(--muted-foreground))] truncate">
              {session?.user?.email}
            </p>
          </div>
        </div>
        <Button
          variant="ghost"
          size="sm"
          onClick={() => signOut({ callbackUrl: "/login/admin" })}
          className="w-full justify-start text-red-600 hover:text-red-700 hover:bg-red-50"
        >
          <LogOut className="w-4 h-4 mr-2" />
          Sign Out
        </Button>
      </div>
    </div>
  )
}

function LoadingSkeleton() {
  return (
    <div className="min-h-screen bg-[hsl(var(--background))] flex items-center justify-center">
      <div className="w-8 h-8 border-4 border-[hsl(var(--muted))] border-t-slate-900 rounded-full animate-spin" />
    </div>
  )
}

export default function AdminLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const { data: session, status } = useSession()
  const router = useRouter()
  const pathname = usePathname()
  const [sheetOpen, setSheetOpen] = useState(false)

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login/admin")
    }
  }, [status, router])

  // Close sheet on route change
  useEffect(() => {
    setSheetOpen(false)
  }, [pathname])

  if (status === "loading") {
    return <LoadingSkeleton />
  }

  if (!session || session.user.role !== "admin") {
    return <LoadingSkeleton />
  }

  return (
    <div className="min-h-screen bg-[hsl(var(--muted))] flex">
      {/* Desktop Sidebar */}
      <aside className="hidden lg:flex lg:w-64 lg:flex-col lg:fixed lg:inset-y-0 bg-[hsl(var(--card))] border-r border-[hsl(var(--border))]">
        <SidebarContent pathname={pathname} />
      </aside>

      {/* Mobile Header + Sheet */}
      <div className="lg:hidden fixed top-0 left-0 right-0 z-40 bg-[hsl(var(--card))] border-b border-[hsl(var(--border))] h-14">
        <div className="flex items-center justify-between px-4 h-full">
          <Sheet open={sheetOpen} onOpenChange={setSheetOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon">
                <Menu className="w-5 h-5" />
              </Button>
            </SheetTrigger>
            <SheetContent side="left" className="p-0 w-64">
              <SidebarContent pathname={pathname} onNavigate={() => setSheetOpen(false)} />
            </SheetContent>
          </Sheet>

          <div className="flex items-center gap-2">
            <div className="w-8 h-8 bg-slate-800 rounded-lg flex items-center justify-center">
              <Shield className="w-4 h-4 text-white" />
            </div>
            <span className="font-semibold text-[hsl(var(--foreground))]">Admin</span>
          </div>

          <div className="w-10" /> {/* Spacer */}
        </div>
      </div>

      {/* Main Content */}
      <main className="flex-1 lg:pl-64">
        <div className="pt-14 lg:pt-0 min-h-screen">
          {children}
        </div>
      </main>
    </div>
  )
}
